export default {
    data(){
        return {
            children: '',
            phone: '1232233332',
            colors: ['#7EC0EE', '#BCD2EE', '#EE9572']
        }
    },
    methods: {
        getChildren(){
            for(let i = 0; i < 3; i++){
                this.children += `
                                <div id="container" style="background-color: ${this.colors[i > this.colors.length - 1 ? Math.floor(Math.random() * this.colors.length) : i]}">
                                    <img id="avator" src="../assets/logo.png" alt="头像"/>
                                    <div id="info-box">
                                        <span>姓名:章三</span><br/>
                                        <span>年龄:6个月</span><br/>
                                        <span>vip有效期:2018年12月30日</span>
                                    </div>
                                    <div id="right">
                                        <div id="b-code">激活码激活</div>
                                        <div id="b-vip">马上开通</div>
                                    </div>
                                </div>
                                `;
            }
        },
        clickCode(id){
            //跳转激活码界面
        },
        clickVip(id){
            //跳转开会员界面
        }
    },
    mounted(){
        this.getChildren();
        let localPhone = window.localStorage.getItem('phone');
        localPhone = localPhone.trim().replace(' ', '');
        this.phone = localPhone.substring(0, 3) + 'xxxx' + localPhone.substring(7, localPhone.length);
    }
}



